import React, { useEffect, useRef } from 'react';
import FormItems from './formItems';
import { Provider } from './hooks/context';
import { cloneDeep } from './utils';
import TextArea from '../TextArea';
import Slider from '../Slider';
import SearchBar from '../SearchBar';
import Stepper from '../Stepper';
import Input from './comps/input';
import Rating from './comps/rate';
import Radio from './comps/radio';
import Switch from './comps/switch';
import CheckBox from './comps/checkBox';
const Form = baseProps => {
  const {
    schema,
    form,
    initialValues = {},
    mode = 'default',
    watch,
    customComponentList = {},
    changeValidate = false
  } = baseProps;
  const isMount = useRef();
  const innerMethods = form.getInnerMethods(true);
  useEffect(() => {
    if (!isMount.current) {
      innerMethods.updateStore({
        initialValues: cloneDeep(initialValues),
        store: initialValues
      });
    }
  }, []);
  useEffect(() => {
    isMount.current = true;
  }, []);
  const contextProps = {
    innerMethods: innerMethods,
    mode: mode,
    watch: watch,
    customComponentList: {
      ...customComponentList,
      input: <Input />,
      textArea: <TextArea />,
      slider: <Slider />,
      rate: <Rating disabled={false} />,
      radio: <Radio />,
      search: <SearchBar />,
      switch: <Switch />,
      checkBox: <CheckBox />,
      stepper: <Stepper value={0} onChange={() => {}} />
    },
    changeValidate: changeValidate
  };
  return <Provider contextProps={contextProps}>
      <FormItems schema={schema} />
    </Provider>;
};
export default Form;